@use "../_core/_core.scss" as *; // コアファイル群

.calendar {
  $colorHoliday: #db7064;
  $colorGray: #5e5e5e;
  $colorGray-light: #ddd;
  border-top: 1px solid $colorGray-light;
  border-left: 1px solid $colorGray-light;
  width: 19.7rem;
  text-align: center;

  background-color: #fff;
  h3 {
    margin: 0;
    color: #fff;
    background-color: $colorGray;
    span {
      font-size: 2rem;
    }
  }
  &-grid {
    display: grid;
    grid-template-columns: repeat(7, 1fr);
  }
  &-header {
    padding: 5px;
    font-weight: 500;
    color: #6c757d;
  }
  &-legend {
    padding-left: 1rem;
    font-size: 1.2rem;
    line-height: 1;
    gap: 0.5rem;
  }
  .day {
    border-right: 1px solid #ddd;
    border-bottom: 1px solid #ddd;
    font-family: Roboto, sans-serif;
    font-size: 1.4rem;
    text-align: center;
  }
  .day-header {
    border-right: 1px solid #ddd;
    border-bottom: 1px solid #ddd;
    font-size: 1.4rem;
    background-color: #ebebeb;
  }
  .day.holiday {
    color: #fff;
    background-color: $colorHoliday;
  }
  .day.empty {
    visibility: hidden;
  }
  .other-month-day {
    color: #ccc;
  }
  .day.today {
    font-weight: 700;
    background-color: #f0f0f0;
  }
  .day.saturday {
    color: #0000ff;
  }
  .day.sunday {
    color: #fff;
    background-color: $colorHoliday;
  }

  // ↓SP相当幅
  @media #{$media-sp} {
    &-legend {
      padding-top: 1rem;
      padding-left: 0;
    }
  }
  // ↑SP相当幅
}
.holiday-box {
  border: 1px solid #dadada;
  width: 1.2rem;
  height: 1.2rem;
  background-color: #db7064;
}

