@use "_core/_core.scss" as *; // コアファイル群
.footer {
  position: relative;
  z-index: 1;
  border-top: 1px solid var(--line-gray, #dadada);
  background-color: var(--background-gray, #f2f2f2);

  &__left {
    max-width: 52%;
    gap: 3rem;
  }
  &__right {
    width: 59.5rem;
    max-width: calc(48% - 2em);
  }
  &__col {
    width: 29.5rem;
  }
  &__ttl {
    margin-bottom: 2rem;
    padding-bottom: 0.5rem;
    border-bottom: 1px solid $fontColor;
    font-size: 1.8rem;
    font-weight: 500;
  }
  &__img {
    padding-top: 1rem;
    img {
      margin-right: 2rem;
      padding-bottom: 1rem;
    }
  }

  // ↓SP相当幅
  @media #{$media-sp} {
    &:has(.navbar) {
      padding-bottom: 6rem;
    }
    &__left {
      max-width: 100%;
      gap: 1rem;
    }
    &__right {
      width: 100%;
      max-width: 100%;
    }
    &__col {
      width: 100%;
    }
    &__ttl {
      font-size: 1.6rem;
    }
    &__img {
      img {
        height: 4.4rem;
      }
    }
    &__txt {
      font-size: 1.4rem;
    }
  }
  // ↑SP相当幅
}
.footerLeg {
  position: absolute;
  top: 0;
  right: 0;
  z-index: -1;
}
.footerTop {
  padding-top: 5rem;
  padding-bottom: 7rem;
  gap: 1rem;
  // ↓SP相当幅
  @media #{$media-sp} {
    padding-top: 3rem;
    padding-bottom: 5rem;
  }
  // ↑SP相当幅
}
.footerBottom {
  padding-top: 3rem;
  padding-bottom: 2rem;
  background-color: #fff;
  // ↓SP相当幅
  @media #{$media-sp} {
    padding-top: 2rem;
  }
  // ↑SP相当幅
}
.footerNav {
  padding-bottom: 5rem;
  &__link {
    padding-right: 2rem;
    padding-left: 2rem;
    border-left: 1px solid $colorGray;
    font-size: 1.323rem;
    color: $fontColor;
    &:last-child {
      border-right: 1px solid $colorGray;
    }
  }
  // ↓SP相当幅
  @media #{$media-sp} {
    padding-bottom: 2rem;
    &__link {
      margin-bottom: 1rem;
      padding-right: 1rem;
      padding-left: 1rem;
      font-size: 1.2rem;
    }
  }
  // ↑SP相当幅
}
.footerContact {
  &__txt {
    font-size: min(1.4rem, 1vw);
    font-weight: 500;
  }
  &__tel {
    gap: 1rem;
    span {
      &:nth-child(1) {
        padding: 0 0.5rem;
        border-radius: 0.3rem;
        font-weight: 500;
        line-height: 1.4;
        color: #fff;
        background-color: $siteColor;
      }
      &:nth-child(2) {
        font-size: min(2.8rem, 2vw);
        font-weight: 600;
        color: $siteColor;
      }
    }
  }
  // ↓SP相当幅
  @media #{$media-sp} {
    gap: 1rem;
    &__txt {
      font-size: 1.2rem;
    }
    &__tel {
      span {
        &:nth-child(1) {
          padding: 0 0.5rem;
        }
        &:nth-child(2) {
          font-size: 2rem;
        }
      }
    }
  }
  // ↑SP相当幅
}
.footerCalendar {
  padding-top: 2rem;
  padding-bottom: 2rem;
  &__wrap {
    gap: 1.5rem;
  }
  &__item {
    width: 19.7rem;
    max-width: 50%;
  }
  // ↓SP相当幅
  @media #{$media-sp} {
    &__wrap {
      gap: 1rem;
    }
  }
  // ↑SP相当幅
}
.footerCopy {
  padding-top: 1.7rem;
  font-size: 0.8864rem;
  color: $colorGray;
  // ↓SP相当幅
  @media #{$media-sp} {
  }
  // ↑SP相当幅
}

