.navbar {
  position: fixed;
  bottom: 0;
  z-index: 99;
  margin: 0;
  width: 100%;
  &__item {
    flex: 1;
    padding: 1rem;
    padding: 1.6rem 0.2rem;
    height: 6rem;
    font-size: 1.4rem;
    font-weight: 400;
    color: #fff;
    text-align: center;
    text-decoration: none;
    .icon {
      font-size: 120%;
    }
    &--signup,
    &--mypage {
      border: 1px solid #dadada;
      color: #333;
      background: #f2f2f2;
    }
    &--login {
      background: #333;
    }
    &--favorite {
      background: #e2a8a4;
    }
    &--cart {
      background: $siteColor;
    }
    &:hover {
      opacity: 1;
    }
  }
}
