@use "../_core/_core.scss" as *; // コアファイル群

.e-form {
  $color-light-red: #d2493f;
  $color-line-gray: #dadada;

  padding-top: 4rem;
  padding-bottom: 2rem;
  &__title {
    margin-bottom: 5rem;
    padding-bottom: 2rem;
    border-bottom: 2px solid #333;
    font-size: 3rem;
    font-weight: 600;
  }
  &__row {
    padding-top: 3rem;
    padding-bottom: 3rem;
    border-top: 1px solid $color-line-gray;
    &--last {
      margin-bottom: 3rem;
      border-bottom: 1px solid $color-line-gray;
    }
    &--inline {
      gap: 2rem;
    }
  }
  &__field {
    flex: 1;
  }
  &__label {
    margin-bottom: 0.8rem;
    font-size: 1.8rem;
    font-weight: 500;
  }
  &__required {
    display: flex;
    align-items: center;
    margin-left: 1rem;
    padding: 0 0.5rem;
    border: 1px solid $color-light-red;
    border-radius: 0.2rem;
    height: 2rem;
    font-size: 1.4rem;
    font-weight: 500;
    color: $color-light-red;
  }
  &__input {
    box-sizing: border-box;
    padding: 0.8rem;
    border: 1px solid #626161;
    border-radius: 0.4rem;
    width: 100%;
    &::placeholder {
      font-weight: 400;
      color: $color-line-gray;
    }
    &--zipcode {
      padding: 0.8rem;
      border-radius: 0.5rem;
      width: 16.4rem;
      font-weight: 700;
    }
  }
  &__address-box {
    margin-right: 1.5rem;
    width: 19.6rem;
  }
  &__address-txt {
    padding-right: 1rem;
    font-size: 2rem;
    font-weight: 700;
  }
  &__note {
    padding-left: 1.5rem;
    font-size: 1.4rem;
    font-weight: 500;
    line-height: 1.2;
    color: $color-light-red;
  }
  &__date-fields {
    display: flex;
    gap: 1rem;
    font-size: 2rem;
    font-weight: 500;
    .e-form__select {
      width: 15rem;
    }
  }
  &__select {
    padding: 0.8rem;
    border: 1px solid #626161;
    border-radius: 0.4rem;
  }
  &__radio-group {
    gap: 2rem;
  }
  &__radio-label {
    cursor: pointer;
    gap: 0.5rem;
  }
  &__radio-input {
    flex-shrink: 0;
    width: 2.5rem;
    height: 2.5rem;
  }
  &__message {
    margin-bottom: 5rem;
    padding: 2rem 1.5rem;
    border-radius: 0.5rem;
    font-weight: 500;
    line-height: 1.4;
    background-color: #f2f2f2;
  }
  &__actions {
    margin-top: 3rem;
  }
  &__checkbox-label {
    display: flex;
    align-items: center;
    margin-bottom: 3rem;
    font-size: 1.6rem;
    font-weight: 500;
  }
  &__checkbox-input {
    margin-right: 0.8rem;
    border: 1px solid $color-line-gray;
    border-radius: 0.5rem;
    width: 2.5rem;
    height: 2.5rem;
  }
  &__button {
    padding: 1rem;
    border: none;
    border-radius: 0.4rem;
    font-size: 2rem;
    font-weight: 600;
    transition: background-color 0.3s;
    cursor: pointer;
    &--primary {
      padding: 1.6rem 1.8rem;
      border-radius: 0.5rem;
      width: 100%;
      max-width: 30rem;
      height: 7.5rem;
      color: #fff;
      background: var(--font-black, #333);
    }
    &--secondary {
      border: 2px solid $siteColor;
      border-radius: 1rem;
      width: 13rem;
      font-size: 1.4rem;
      font-weight: 700;
      line-height: 1.4;
      color: #333;
      background-color: #fff;
    }
  }
  &__link {
    color: #229fff;
    text-decoration: underline;
  }

  .my-1 {
    margin-top: 1rem;
    margin-bottom: 1rem;
  }
  .mb-1 {
    margin-bottom: 1rem;
  }

  // ↓SP相当幅
  @media #{$media-sp} {
    padding-top: 4rem;
    padding-bottom: 2rem;
    &__title {
      margin-bottom: 4rem;
      padding-bottom: 1.5rem;
      font-size: 2rem;
    }
    &__row {
      padding-top: 2rem;
      padding-bottom: 2rem;
      &--last {
        margin-bottom: 2rem;
      }
      &--inline {
        gap: 1rem;
      }
    }
    &__label {
      font-size: 1.6rem;
    }
    &__required {
      font-size: 1.2rem;
    }
    &__input {
      &::placeholder {
        font-size: 1.4rem;
      }
      &--zipcode {
        width: 12.5rem;
      }
    }
    &__address-box {
      width: 15rem;
    }
    &__address-txt {
      font-size: 1.6rem;
    }
    &__note {
      font-size: 1.4rem;
    }
    &__date-fields {
      font-size: 1.6rem;
    }
    &__radio-input {
      width: 2.1rem;
      height: 2.1rem;
    }
    &__message {
      margin-bottom: 3rem;
      padding: 1.5rem 1rem;
      font-size: 1.4rem;
    }
    &__actions {
      margin-top: 3rem;
    }
    &__checkbox-label {
      margin-bottom: 3rem;
      font-size: 1.6rem;
    }
    &__checkbox-input {
      width: 2.1rem;
      height: 2.1rem;
    }
    &__button {
      font-size: 1.6rem;
      &--primary {
        width: 27rem;
        height: 6rem;
      }
      &--secondary {
        font-size: 1.4rem;
      }
    }
  }
  // ↑SP相当幅
}
