@use "_core/_core.scss" as *; // コアファイル群
/*
タグそのものに対する総合的なスタイル定義
フォントサイズ指定などもあるので一部はサイトに応じて調整する
*/

//  #####  #####      ###    ###    ###  #####
//  ##     ##  ##    ## ##   ## #  # ##  ##
//  #####  #####    ##   ##  ##  ##  ##  #####
//  ##     ##  ##   #######  ##      ##  ##
//  ##     ##   ##  ##   ##  ##      ##  #####
html {
  box-sizing: border-box;
  font-size: 62.5%; // 62.5%で1rem=10px

  /*
複雑なレイアウト用のVW指定
*/
  // font-size: $pcVw;
  // @media #{$media-sp} {
  //   font-size: $spVw;
  // }

  // TOPだけなど、特定のページのみ適用する場合はここではなく該当ページのHTMLに<style>で書く
  // <style>
  //     html {
  //         font-size: 0.52vw;
  //     }
  //     @media screen and (max-width:768px) {
  //         html {
  //             font-size: 2.65vw;
  //         }
  //     }
  // </style>
}
*,
*:after,
*:before {
  box-sizing: inherit;
}

body {
  width: 100%;
  min-height: 100dvh; // 常に画面分の高さを確保
}

//  ##  ###    ###   ####
//  ##  ## #  # ##  ##
//  ##  ##  ##  ##  ##  ###
//  ##  ##      ##  ##   ##
//  ##  ##      ##   ####
img {
  box-sizing: content-box;
  max-width: 100%;
  height: auto;
  vertical-align: middle; //インライン画像のベースライン余白除去
}

//  ######  #####  ##    ##  ######
//    ##    ##      ##  ##     ##
//    ##    #####    ####      ##
//    ##    ##      ##  ##     ##
//    ##    #####  ##    ##    ##
body {
  line-height: 1.8;
}
body,
button,
input,
select,
textarea {
  font-family: $sans_serif;
  font-size: 1.6rem;
  font-weight: 400;
  color: $fontColor;
  word-wrap: break-word;
}

a {
  color: $siteColor;
  text-decoration: none;
  transition-duration: 0.3s;
  &:link {
  }
  &:visited {
  }
  &:hover {
  }
  &:active,
  &:focus {
  }
}
p:not([class], :last-child) {
  margin-bottom: 1em;
}

::selection {
  //選択範囲の装飾設定
  color: #fff;
  background-color: darken($siteColor, 5%);
}

b,
strong {
  font-weight: 700;
}
sup {
  font-size: 0.7em;
  vertical-align: super;
}

//  ######    ###    #####   ##      #####
//    ##     ## ##   ##  ##  ##      ##
//    ##    ##   ##  #####   ##      #####
//    ##    #######  ##  ##  ##      ##
//    ##    ##   ##  #####   ######  #####
table {
  width: 100%;
  max-width: 100%;
  p:not([class], :last-child) {
    margin-bottom: 1em;
  }
}
td,
th {
  min-height: 1em;
  word-break: break-all;
  word-wrap: break-word;
}

//  #####   #####   #####    ###    ###
//  ##     ##   ##  ##  ##   ## #  # ##
//  #####  ##   ##  #####    ##  ##  ##
//  ##     ##   ##  ##  ##   ##      ##
//  ##      #####   ##   ##  ##      ##

// プレースホルダー（例文）の設定
input,
textarea {
  &::placeholder {
    color: #c1c1c1;
  }
}

textarea {
  resize: vertical; // 縦方向以外にリサイズさせない
}

// アウトラインを消して背景を初期化
[type="email"],
[type="password"],
[type="search"],
[type="tel"],
[type="text"],
[type="url"],
textarea,
button,
select {
  background: 0;
  outline: none;
}

//   ####  ##   ##  #####     ####   #####   #####
//  ##     ##   ##  ##  ##   ##     ##   ##  ##  ##
//  ##     ##   ##  #####     ###   ##   ##  #####
//  ##     ##   ##  ##  ##      ##  ##   ##  ##  ##
//   ####   #####   ##   ##  ####    #####   ##   ##
// クリッカブル要素でマウスカーソルを指カーソルに
a,
area,
[type="checkbox"],
[type="radio"],
[type="submit"],
button,
select,
label,
summary {
  cursor: pointer;
}
