@use "_core/_core.scss" as *; // コアファイル群
.secMV {
  position: relative;
  padding-top: 7rem;
  padding-bottom: 5rem;
  &-wrap {
    position: relative;
  }
  &-slider {
    margin: 0 0 1rem;
    .swiper-slide {
      img {
        height: 40rem;
        object-fit: cover;
      }
    }
  }

  //サムネイルスライド
  &__thumb {
    margin: auto;
    margin-top: 3rem;
    padding: 2.5rem 4rem;
    border-radius: 1rem;
    max-width: 104.8rem;
    overflow: hidden;
    background-color: #f2f2f2;

    .swiper-slide {
      padding-top: 0.9rem;
      overflow: hidden;
      text-align: center;
      &::before {
        display: none;
        position: absolute;
        top: 0;
        left: 50%;
        width: 1.2rem;
        height: 1rem;
        background: url(../img/icon_triangle.svg) no-repeat center;
        transform: translateX(-50%);
        content: "";
      }
      img {
        border-width: 4px;
        border-style: solid;
        border-color: transparent;
        height: 6rem;
      }
      &-thumb {
        &-active {
          opacity: 1;
          &::before {
            display: block;
          }
          img {
            border-color: #e2a8a4;
          }
        }
      }
    }
  }
  // ↓SP相当幅
  @media #{$media-sp} {
    padding-top: 3rem;
    padding-bottom: 3rem;
    &-slider {
      margin: 0 0 1rem;
      .swiper-slide {
        img {
          height: auto;
        }
      }
    }

    //サムネイルスライド
    &__thumb {
      margin: auto;
      margin-top: 3rem;
      padding: 2.5rem 4rem;
      border-radius: 1rem;
      width: 107.8rem;
      overflow: hidden;
      background-color: #f2f2f2;

      .swiper-slide {
        padding-top: 0.9rem;
        overflow: hidden;
        text-align: center;
        &::before {
          display: none;
          position: absolute;
          top: 0;
          left: 50%;
          width: 1.2rem;
          height: 1rem;
          background: url(../img/icon_triangle.svg) no-repeat center;
          transform: translateX(-50%);
          content: "";
        }
        img {
          border-width: 4px;
          border-style: solid;
          border-color: transparent;
          height: 6rem;
        }
        &-thumb {
          &-active {
            opacity: 1;
            &::before {
              display: block;
            }
            img {
              border-color: #e2a8a4;
            }
          }
        }
      }
    }
  }
  // ↑SP相当幅
}

