.pageBtn {
  position: relative;
  z-index: 1;
  padding: 0 2rem;
  border: 2px solid $siteColor;
  border-radius: 1rem;
  width: 29.5rem;
  height: 6.5rem;
  overflow: hidden;
  font-size: 1.8rem;
  font-weight: 700;
  color: $fontColor;
  text-align: center;
  background-color: #fff;
  transition-duration: 0.3s;
  gap: 1rem;
  .icon {
    padding-top: 0.2rem;
    transition-duration: 0.3s;
  }
  &:hover {
    color: $siteColor;
    .icon {
      transform: translateX(0.1rem);
    }
  }

  // ↓SP相当幅
  @media #{$media-sp} {
    width: 27rem;
    height: 5.5rem;
    font-size: 1.6rem;
  }
  // ↑SP相当幅
}

