  margin-bottom: 3rem;
  padding-top: 2rem;
  padding-bottom: 1.5rem;
  border-bottom: 1px solid $fontColor;
  font-size: 2.4rem;
  font-weight: 700;
  // ↓SP相当幅
  @media #{$media-sp} {
    padding-bottom: 1rem;
    font-size: 1.8rem;
  }
  // ↑SP相当幅
}

