.secBtn {
  padding-top: 10rem;
  padding-bottom: 12rem;
  &__wrap {
    gap: 2.7rem;
  }
  &__item {
    position: relative;
    padding: 0 2rem;
    border: 2px solid $siteColor;
    border-radius: 1rem;
    width: 40rem;
    height: 8rem;
    text-align: center;
    background-color: #fff;
    &--pet {
      position: absolute;
      left: 50%;
      transform: translateX(-50%);
      &Dog {
        top: -6.9247rem;
      }
      &Cat {
        top: -7.2rem;
      }
    }
    &--link {
      font-size: 2.4rem;
      font-weight: 700;
      letter-spacing: 0.48rem;
    }
  }

  // ↓SP相当幅
  @media #{$media-sp} {
  }
  // ↑SP相当幅
}

