.productItem {
  overflow: hidden;
  &__txt {
    margin-bottom: 1rem;
    padding: 0.5rem;
    border-radius: 0.3rem;
    line-height: 1.5;
    color: #626161;
    background-color: #f2f2f2;
  }
  &__img {
    border: 1px solid var(--line-gray, #dadada);
    border-radius: 0.3rem;
    overflow: hidden;
    img {
      transition-duration: 0.3s;
    }
  }
  &__ttl {
    padding-top: 0.5rem;
    padding-bottom: 1rem;
    font-weight: 600;
    color: #000;
  }
  &__price {
    font-weight: 400;
    color: #000;
  }
  &__tax {
  }
  &__new {
    padding: 2px 0.5rem 0;
    border-radius: 0.2rem;
    font-size: 1.4rem;
    line-height: 1.4;
    color: #fff;
    background-color: $colorRed;
  }
  &:hover {
    .productItem__txt {
      color: $siteColor;
    }
    .productItem__img {
      img {
        transform: scale(1.0555);
      }
    }
    .productItem__ttl {
      text-decoration: underline;
    }
  }
  // ↓SP相当幅
  @media #{$media-sp} {
    &__txt {
      font-size: 1.2rem;
    }
    &__ttl {
      font-size: 1.4rem;
      line-height: 1.4;
    }
    &__price {
      font-size: 1.4rem;
    }
    &__new {
      font-size: 1.2rem;
    }
  }
  // ↑SP相当幅
}
.productBtn {
  position: absolute;
  top: 50%;
  z-index: 1;
  transform: translateY(-50%);
  cursor: pointer;
  &-left {
    left: -3rem;
  }
  &-right {
    right: -3rem;
  }
  // ↓SP相当幅
  @media #{$media-sp} {
  }
  // ↑SP相当幅
}

