.secBrand {
  padding-top: 2rem;
  &__btn {
    padding-top: 3rem;
  }
  .productItem {
    &__ttl {
      text-align: center;
    }
    &__img {
      text-align: center;
      @media #{$media-notsp} {
        img {
          height: 23.4rem;
        }
      }
      img {
        aspect-ratio: 3 / 2;
        object-fit: contain;
      }
    }
  }
  .productBtn {
    top: 40%;
  }
  // ↓SP相当幅
  @media #{$media-sp} {
    padding-top: 2rem;
    &__btn {
      padding-top: 1rem;
    }
  }
