.secBanner {
  padding-top: 4rem;
  padding-bottom: 6rem;
  &__list {
    gap: 2rem;
  }
  &__item {
    flex: 1 1 calc(33.3333% - 2rem);
    &.-w100 {
      flex: 1 1 100%;
      max-width: 100%;
    }
  }
  // ↓SP相当幅
  @media #{$media-sp} {
    padding-top: 1rem;
    padding-bottom: 2rem;
    &__item {
      flex: 1 1 100%;
      max-width: 100%;
    }
  }
  // ↑SP相当幅
}

