@use "../_core/_core.scss" as *; // コアファイル群

.deliveryList {
  margin: 16px auto;
  padding: 24px 16px 32px;
  border-radius: 10px;
  max-width: 950px;
  font-family: "Segoe UI", "Meiryo", sans-serif;
  background: #fff;
  box-shadow: 0 0 0 1px #ddd;

  &__header {
    margin-bottom: 18px;
    padding-bottom: 8px;
    border-bottom: 3px solid #444;
  }
  &__title {
    margin: 0;
    font-size: 1.7rem;
    font-weight: bold;
  }

  &__section {
    margin-bottom: 38px;
    &:last-child {
      margin-bottom: 0;
    }
  }

  &__section-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 16px;
    margin-bottom: 10px;
  }
  &__section-title {
    margin: 0;
    font-size: 1.25rem;
    font-weight: 600;
    color: #232323;
  }

  &__action-btn {
    display: flex;
    align-items: center;
    padding: 0 18px;
    border: 2px solid #1565c0;
    border-radius: 7px;
    height: 38px;
    font-size: 15px;
    font-weight: 600;
    color: #1565c0;
    background: #fff;
    outline: none;
    transition: background 0.15s, border-color 0.13s;
    cursor: pointer;
    gap: 8px;
    &:hover,
    &:focus {
      border-color: #1976d2;
      color: #1976d2;
      background: #e3f2fd;
    }
  }
  &__action-icon {
    display: inline-block;
    margin-left: 4px;
    font-size: 15px;
    font-weight: bold;
  }

  &__table-wrap {
    margin-top: 8px;
    border: 1px solid #e0e0e0;
    border-radius: 6px;
    overflow-x: auto;
    background: #fafafa;
  }
  &__table {
    width: 100%;
    background: none;
    border-collapse: collapse;
  }
  &__th {
    padding: 11px 14px;
    border-right: 1px solid #e0e0e0;
    border-bottom: 1px solid #e0e0e0;
    width: 160px;
    min-width: 110px;
    font-size: 15px;
    font-weight: 500;
    color: #555;
    text-align: left;
    vertical-align: middle;
    background: #efefef;
  }
  &__td {
    padding: 11px 16px;
    border-bottom: 1px solid #e0e0e0;
    font-size: 15px;
    color: #333;
    text-align: left;
    vertical-align: middle;
    background: #fff;
  }
  &__table tr:last-child &__th,
  &__table tr:last-child &__td {
    border-bottom: none;
  }
}
