@use "../_core/_core.scss" as *; // コアファイル群

.form {
  margin: 18px auto;
  padding: 24px 20px 40px;
  border-radius: 10px;
  max-width: 900px;
  font-family: "Segoe UI", "Meiryo", sans-serif;
  background: #fff;
  box-shadow: 0 0 0 1px #ddd;

  &__header {
    margin-bottom: 22px;
    padding-bottom: 8px;
    border-bottom: 3px solid #444;
  }

  &__title {
    margin: 0;
    font-size: 1.7rem;
    font-weight: bold;
  }

  &__body {
    margin-top: 22px;
  }

  &__group {
    display: flex;
    flex-direction: column;
    margin-bottom: 28px;

    &--row {
      flex-direction: row;
      gap: 24px;
      align-items: flex-start;
    }
    &--birth {
      align-items: center;
      gap: 7px;
      margin-bottom: 19px;
    }
    &--gender {
      align-items: center;
      gap: 25px;
      margin-bottom: 12px;
    }
  }

  &__field {
    display: flex;
    flex: 1 1 0;
    flex-direction: column;
    min-width: 0;
  }

  &__label {
    display: flex;
    align-items: center;
    margin-bottom: 4px;
    font-size: 16px;
    font-weight: 500;
    color: #232323;
    gap: 4px;
  }

  &__required {
    display: inline-block;
    margin-left: 5px;
    padding: 0 5px;
    border: 1px solid #ffd4d4;
    border-radius: 3px;
    font-size: 13px;
    font-weight: 500;
    line-height: 1.2;
    color: #e53935;
    background: #fff0f0;
  }

  &__input {
    margin-bottom: 0;
    padding: 10px 14px;
    border: 1.5px solid #bbb;
    border-radius: 7px;
    width: 100%;
    font-size: 16px;
    background: #fafafa;
    outline: none;
    transition: border-color 0.13s;

    &:focus {
      border-color: #1976d2;
      background: #fff;
    }
    &--zip {
      margin-right: 10px;
      width: 130px;
    }
    &--pref {
      margin-top: 8px;
      margin-bottom: 7px;
      width: 180px;
    }
    &--address {
      margin-top: 8px;
      margin-bottom: 0;
    }
    &--birth {
      margin: 0 2px;
      padding: 8px 7px;
      width: 95px;
      font-size: 15px;
    }
  }

  &__address-row {
    display: flex;
    align-items: center;
    margin-bottom: 9px;
    gap: 7px;
  }
  &__address-icon {
    margin-right: 2px;
    font-family: inherit;
    font-size: 22px;
    color: #888;
  }
  &__address-btn {
    margin-left: 5px;
    padding: 0 12px;
    border: 2px solid #1565c0;
    border-radius: 7px;
    height: 34px;
    font-size: 14px;
    font-weight: 600;
    color: #1565c0;
    background: #fff;
    outline: none;
    transition: background 0.15s, border-color 0.13s;
    cursor: pointer;
    &:hover,
    &:focus {
      border-color: #1976d2;
      background: #e3f2fd;
    }
  }

  &__birth-unit {
    margin: 0 2px 0 0;
    font-size: 16px;
    color: #232323;
  }

  &__radio-label {
    display: flex;
    align-items: center;
    margin-right: 18px;
    font-size: 16px;
    font-weight: 400;
    color: #232323;
    gap: 5px;
  }
  &__radio {
    accent-color: #1976d2;
    margin-right: 4px;
    width: 19px;
    height: 19px;
    vertical-align: middle;
  }

  &__note {
    margin-top: 18px;
    margin-bottom: 18px;
    padding: 10px 13px 13px;
    border: 1px solid #e2e2e2;
    border-radius: 5px;
    font-size: 13px;
    line-height: 1.45;
    color: #232323;
    background: #f8f8f8;
  }

  &__agree {
    margin-top: 6px;
    margin-bottom: 30px;
  }
  &__agree-label {
    display: flex;
    align-items: center;
    font-size: 15px;
    font-weight: 400;
    color: #232323;
    gap: 8px;
  }
  &__checkbox {
    margin-right: 5px;
    width: 19px;
    height: 19px;
    vertical-align: middle;
    accent-color: #1976d2;
  }
  &__link {
    margin-right: 3px;
    font-size: 15px;
    color: #1976d2;
    text-decoration: underline;
  }
  &__submit-btn {
    display: block;
    margin-top: 10px;
    margin-right: auto;
    margin-left: auto;
    padding: 14px 0;
    border: none;
    border-radius: 7px;
    width: 320px;
    max-width: 100%;
    font-size: 18px;
    font-weight: 600;
    color: #fff;
    letter-spacing: 0.03em;
    background: #232323;
    transition: background 0.13s;
    cursor: pointer;
    &:hover,
    &:focus {
      background: #444;
    }
  }
}
