@use "../_core/_core.scss" as *; // コアファイル群
$mbLayAnime: $animeTime;
.mdLay {
  display: flex;
  position: relative;
  z-index: 1;
  vertical-align: middle;
  transition-duration: $mbLayAnime;
  transform: perspective(0) translate3d(0, 0, 0);
  &__bg {
    position: relative;
    z-index: 2;
    width: 100%;
    transition-duration: $mbLayAnime;
    img {
      display: block;
      width: 100%;
    }
  }
  //ズーム効果をつける
  &.zoom {
    overflow: hidden;
  }

  &__box {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 5;
    width: 100%;
    height: 100%;
  }
}
button.active,
a.active {
  .mdLay {
    &.zoom .mdLay__bg {
      scale: 1.1;
    }
  }
}
// ↓SP以外の幅
@media #{$media-notsp} {
  button.hover,
  a.hover,
  a:hover {
    .mdLay {
      &.zoom .mdLay__bg {
        scale: 1.1;
      }
    }
  }
}
// ↑SP以外の幅
