@use "../_core/_core.scss" as *; // コアファイル群

.p-greenies {
  &__banner {
    padding: 3.6rem 0 2.4rem;
  }
  &__products {
    margin: auto;
    padding: 3.6rem 0 6rem;
    width: 103rem;
    gap: 1rem;
  }
  &__products-pet {
    width: 49.5rem;
    max-width: calc(50% - 1rem);
  }
  &__lead {
    margin: 2rem auto;
    padding: 10rem 0 1.8rem;
    width: 86.5rem;
    max-width: 100%;
    text-align: center;
    background: url("../img/greenies_pattern.png") no-repeat center top;
    background-size: contain;
    &-text {
      margin-bottom: 3rem;
      padding-bottom: 3rem;
      border-bottom: 1px solid var(--line-gray, #dadada);
      font-family: "Zen Old Mincho";
      font-size: 2.4rem;
    }
    &-desc {
      line-height: 2;
    }
  }
  &__links {
    margin: auto;
    padding: 48px 0 32px;
    width: 86.5rem;
    max-width: 100%;
    background: #fff;
    gap: 3rem;
    &-pet {
      width: 39.3rem;
      max-width: calc(50% - 3rem);
    }
    &-line {
      gap: 1rem;
      padding: 0 1.5rem;
    }
    &-icon {
      position: relative;
      margin-bottom: -1.6rem;
    }
    &-btn {
      width: 39.3rem;
      max-width: 100%;
      font-size: 2.2rem;
      font-weight: 900;
      color: #046e3b;
      text-decoration: none;
      transition: background 0.2s;
      &:hover {
        color: #046e3b;
        .p-greenies__links-img {
          background-size: #ccc;
        }
      }
    }
    &-img {
      transition-duration: 0.3s;
    }
  }
  // ↓SP相当幅
  @media #{$media-sp} {
    &__products {
      padding: 0.6rem 0 3rem;
      width: 100%;
    }
    &__products-pet {
      width: 100%;
      max-width: 100%;
    }

    &__lead {
      margin-top: 0;
      padding: 4rem 0 1.8rem;
      width: 86.5rem;
      max-width: 100%;
      &-text {
        margin-bottom: 2rem;
        padding-bottom: 2rem;
        font-size: 1.8rem;
      }
      &-desc {
        font-size: 1.4rem;
        text-align: left;
      }
    }

    &__links {
      padding: 0 0 2rem;
      width: 100%;
      gap: 2rem;
      &-pet {
        width: 100%;
        max-width: 100%;
      }
      &-line {
        gap: 1rem;
        padding: 0 1.5rem;
      }
      &-icon {
        margin-bottom: -1.8rem;
      }
      &-btn {
        width: 39.3rem;
        font-size: 2.2rem;
        font-weight: 900;
      }
    }
  }
  // ↑SP相当幅
}
