/*
CSSのみで一定速度の無限横スライド
全く同じ構成の.slideshow__listを表示幅を超える数で並べる
アニメ時間は全長で速度が変わるのでHTMLから制御する
.slideshow--hoverStopの付与でホバー時に一時停止
<div class="slideshow flex vcenter">
    <div class="slideshow__list flex vcenter" style="animation-duration: 15s;">
        <div class="slideshow__item">
          テキスト1テキスト1テキスト1テキスト1テキスト1
        </div>
        <div class="slideshow__item">
          テキスト2テキスト2
        </div>
    </div>
    <div class="slideshow__list flex vcenter" style="animation-duration: 15s;">
        <div class="slideshow__item">
          テキスト1テキスト1テキスト1テキスト1テキスト1
        </div>
        <div class="slideshow__item">
          テキスト2テキスト2
        </div>
    </div>
</div>
*/
.slideshow {
  overflow: hidden;
  &__list {
    will-change: translate;
    animation-name: slideshow-loop; // アニメーション名
    animation-timing-function: linear; // アニメーションタイプ
    animation-iteration-count: infinite; // 実行回数 infinite:無限
    animation-fill-mode: both; // アニメ前後のスタイル [none:維持しない] forwards:最終状態を維持 backwards:開始前から初期状態 both:両方
    .slideshow--hoverStop:hover & {
      // ホバー時に停止するオプション
      animation-play-state: paused;
    }
    .slideshow--r & {
      animation-direction: reverse;
    }
  }
  &__item {
    white-space: nowrap;
  }
  @keyframes slideshow-loop {
    0% {
      translate: 0;
    }
    100% {
      translate: -100% 0;
    }
  }
}
