@use "../_core/_core.scss" as *; // コアファイル群
/*
#overview
State

特定のワンポイントに作用させる目的のスタイル
*/

//  ####    #####  ##   ##  ##   ####  #####
//  ##  ##  ##     ##   ##  ##  ##     ##
