/*
#styleguide
ホバー透過　.op

マウスオーバーで不透明度を下げる。ホバー効果に拘る必要がない箇所向け。SP無効。

```
<a href="" class="op"><img src="https://unsplash.it/600/400?random&n06"></a>
```
*/
@media #{$media-notsp} {
  //SP以外の幅
  .op:hover {
    opacity: 0.7;
  }
}
