// PC用のラップサイズクラスを生成する
// @include pcWrapSize(1200); // PC用ラップサイズ .wrap.w○○○ を生成
@mixin pcWrapSize($size: 1200) {
  @media #{$media-notsp} {
    .wrap.w#{$size} {
      max-width: m.div($size, 10) * 1rem + 4rem;
    }
  }
  //Fix for old page
  .wrap.w#{$size} {
    max-width: 100%;
  }
}
