@use "../_core/_core.scss" as *; // コアファイル群

//  #####    ####
//  ##  ##  ##
//  #####   ##
//  ##      ##
//  ##       ####

/*
マージンをセンタリングし、最大幅を制限したラッパー
paddingを左右に入れているため、ラッパーそのものの幅は指定幅+padding分となる

<div class="wrap w1200"></div>

*/

// ↓SP以外の幅
@media #{$media-notsp} {
  .wrap {
    margin-right: auto;
    margin-left: auto;
    padding-right: 2rem;
    padding-left: 2rem;
  }
}
// ↑SP以外の幅

// 必要な幅を追加していく
@include pcWrapSize(1200); // PC用ラップサイズ .wrap.w○○○ を生成
@include pcWrapSize(1070); // PC用ラップサイズ .wrap.w○○○ を生成

//   ####  #####
//  ##     ##  ##
//   ###   #####
//     ##  ##
//  ####   ##
/*
SP用　左右に一定の余白を付けるのみ
<div class="spWrap"></div>

*/
@media #{$media-sp} {
  .spWrap,
  .sp-wrap {
    padding-right: $pad;
    padding-left: $pad;
  }
}

//  ##   ##  ##     ##  ##      ##  #####      ###    #####
//  ##   ##  ####   ##  ##      ##  ##  ##    ## ##   ##  ##
//  ##   ##  ##  ## ##  ##  ##  ##  #####    ##   ##  #####
//  ##   ##  ##    ###  ##  ##  ##  ##  ##   #######  ##
//   #####   ##     ##   ###  ###   ##   ##  ##   ##  ##
/*
親のwidthやpaddingを無視して画面幅いっぱいの領域を確保する
※乱用は避けてください

<div class="wrap w1200 sp-wrap">
    <img loading="lazy" src="https://unsplash.it/300/50?random&n1" style="width:100%">
    <div class="unwrap">
        <img loading="lazy" src="https://unsplash.it/300/50?random&n1" style="width:100%">
    </div>
</div>
*/
.unwrap {
  width: 100vw;
  margin-inline: calc(50% - 50vw);
}
