/*
#overview
SVGアイコンフォント

「img/icon」ディレクトリ内のSVGファイルを元に生成されたアイコンフォント。
タスクランナーの処理で自動的にフォントファイル+SCSSの生成と本ガイドへの記載が行われる。
「i.icon-ファイル名」または「.iconfont（タグ内にファイル名と同じ文字列を入れる）」で呼び出すことができる（後者はIE非対応）。
サンプルコードではCSS追記のフック用に「.icon」クラスも付けている。
*/

@font-face {
  font-family: iconfont;
  src: url("font/icon/iconfont.eot?1eff54e0ee32c0df428b5f67f401f2ad?#iefix") format("embedded-opentype"),
url("font/icon/iconfont.woff2?1eff54e0ee32c0df428b5f67f401f2ad") format("woff2"),
url("font/icon/iconfont.woff?1eff54e0ee32c0df428b5f67f401f2ad") format("woff");
  font-display: swap;
}

.iconfont {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: iconfont !important;
  font-style: normal;
  font-weight: normal !important;
  font-variant: normal;
  text-decoration: none;
  text-transform: none;
}
i[class*="icon-"] {
  display: inline-block;
  line-height: 1;
  vertical-align: middle;
  pointer-events: none;
	&::before {
    @extend .iconfont;
    display: inline-block;
    vertical-align: bottom;
	}
}
/*
#styleguide
.icon-cart
```
<i class="icon-cart"></i>
<i class="icon-cart" style="margin-left:2rem;font-size:2rem"></i>
<i class="icon-cart" style="margin-left:2rem;font-size:5rem"></i>
<span class="iconfont" style="display: inline-block;line-height: 1;vertical-align: middle;margin-left:2rem;font-size:10rem">cart</span>
```
*/
$icon-cart: "\f101";
.icon-cart::before {
  content: "\f101";
}
