@use "../_core/_core.scss" as *; // コアファイル群
//  ###    ###   #####   ####      ###    ##
//  ## #  # ##  ##   ##  ##  ##   ## ##   ##
//  ##  ##  ##  ##   ##  ##  ##  ##   ##  ##
//  ##      ##  ##   ##  ##  ##  #######  ##
//  ##      ##   #####   ####    ##   ##  ######

$closeBtnSize: 5rem; // 閉じるボタンのサイズ
.mdModal {
  position: fixed;
  top: 50%;
  left: 50%;
  z-index: 50000;
  width: fit-content; // 内側の要素に合わせる
  max-width: calc(100% - 4rem);
  transition-duration: 0.3s;
  pointer-events: none;
  &:not([open]) {
    opacity: 0;
    visibility: hidden;
  }
  &__box,
  &__bg,
  a,
  button,
  iframe {
    // クリック要素のみマウスを反応させる
    pointer-events: auto;
  }
  &__bg {
    // 閉じるボタンを兼ねる黒背景
    position: fixed;
    top: 0;
    left: 0;
    z-index: 1;
    width: 100%;
    height: 100%;
    background: rgba(#000, 0.8);
    &:not(.js-modal-close) {
      // 閉じなければカーソルを変えない
      cursor: default;
    }
    &--hide {
      opacity: 0;
    }
  }
  &__frame {
    position: relative;
    z-index: 2;
    transform: translate(-50%, -50%);
  }
  &__close {
    position: absolute;
    top: -$closeBtnSize;
    right: 0;
    z-index: 2;
    width: $closeBtnSize;
    height: $closeBtnSize;
    font-size: 3rem;
    color: #fff;
  }
  &__box {
    display: inline-block;
    position: relative;
    z-index: 2;
    max-height: calc(90vh - $closeBtnSize); // 調整要素
    overflow-y: auto;
    background: #fff; // 調整要素
  }
}
