@use "../_core/_core.scss" as *; // コアファイル群

.product-detail {
  padding-top: 6rem;
  &__title {
    margin-bottom: 6rem;
    padding-bottom: 2.7rem;
    border-bottom: 2px solid #333;
    font-size: 3rem;
    font-weight: 600;
    line-height: 1.4;
    color: #333;
  }
  &__main {
    gap: 6vw;
  }
  &__gallery {
    width: 63.5rem;
    max-width: 50%;
  }
  &__slider {
  }
  &__image {
    height: 63.8rem;
    object-fit: contain;
  }
  &__thumbnail {
    padding-top: 3rem;
    &-img {
      border-width: 2px;
      border-style: solid;
      border-color: transparent;
      height: 9rem;
      object-fit: contain;
    }
    .swiper-slide {
      padding-top: 0.9rem;
      overflow: hidden;
      text-align: center;
      &::before {
        display: none;
        position: absolute;
        top: 0;
        left: 50%;
        width: 1.2rem;
        height: 1rem;
        background: url(../img/icon_triangle.svg) no-repeat center;
        transform: translateX(-50%);
        content: "";
      }
      &-thumb {
        &-active {
          opacity: 1;
          &::before {
            display: block;
          }
          img {
            border-color: #e2a8a4;
          }
        }
      }
    }
  }
  &__info {
    width: 50.2rem;
    max-width: calc(50% - 6vw);
  }
  &__tags {
    margin-bottom: 4rem;
  }
  &__tag {
    display: inline-block;
    padding: 0.2rem 1.2rem;
    border: 1px solid $siteColor;
    border-radius: 0.5rem;
    font-size: 1.4rem;
    font-weight: 500;
    &:nth-child(n + 2) {
      margin-left: 1rem;
    }
  }
  &__price {
    margin-bottom: 2.5rem;
    padding-bottom: 2.5rem;
    border-bottom: 1px solid var(--line-gray, #dadada);
    width: fit-content;
    color: #000;
    &-normal {
      font-size: 1.4rem;
      .-tax {
        font-size: 1.4rem;
      }
    }
    &-new {
      font-size: 4.8rem;
      font-weight: 600;
      line-height: 1.4;
    }
  }
  &__tax {
    font-size: 1.6rem;
  }
  &__code {
    margin-bottom: 0.8rem;
    padding: 0.5rem 1rem;
    border-radius: 0.5rem;
    width: fit-content;
    font-size: 1.2rem;
    background: var(--background-gray, #f2f2f2);
  }
  &__order {
    margin-bottom: 5rem;
    &-type {
      margin-bottom: 8px;
    }
  }
  &__radio {
    margin-right: 2rem;
    width: 12rem;
    font-size: 2rem;
    input {
      margin-right: 1rem;
      width: 2.5rem;
      height: 2.5rem;
    }
  }
  &__qty {
    text-align: center;
    &-label {
      padding-right: 1rem;
      font-size: 2rem;
      font-weight: 300;
    }
    &-btn {
      margin: 0 4px;
      border: 1px solid var(--line-gray, #dadada);
      border-radius: 0.5rem;
      width: 3.5rem;
      font-size: 2rem;
      font-weight: 500;
      color: #000;
      background: var(--background-gray, #f2f2f2);
      cursor: pointer;
    }
    &-input {
      margin: 0 4px;
      padding: 0 2rem;
      border: 1px solid var(--line-gray, #dadada);
      border-radius: 0.5rem;
      width: 8rem;
      font-size: 2.2rem;
      font-weight: 500;
      text-align: center;
    }
  }
  &__subscription-options {
    margin-top: 1rem;
    max-width: 72%;
    input[type="radio"] {
      margin-left: 0;
      width: 1.6rem;
      height: 1.6rem;
    }
  }
  &__subscription-label {
    font-weight: 700;
  }
  &__subscription-title {
    padding-top: 1rem;
    padding-bottom: 0.5rem;
  }
  &__subscription-form {
    padding-left: 1rem;
    border: 1px solid #ccc;
    border-radius: 4px;
    height: 4rem;
    background-color: #fff;
  }
  &__shipping-msg {
    margin-top: 2rem;
    margin-bottom: 1rem;
    font-weight: 500;
    color: #be0000;
  }
  &__actions {
    gap: 2rem;
    margin-bottom: 2rem;
  }
  &__btn {
    flex: 1 1 auto;
    padding: 1.2rem 0;
    border: none;
    border-radius: 0.5rem;
    font-size: 3rem;
    color: #fff;
    transition: background 0.2s;
    cursor: pointer;
    &-cart {
      background: $siteColor;
      &:hover {
        background: darken($siteColor, 5%);
      }
    }
    &-favorite {
      background: #e7bcbc;
      &:hover {
        background: darken(#e7bcbc, 5%);
      }
    }
  }
  &__icon {
    position: relative;
    bottom: 0.5rem;
    margin-right: 0.8rem;
    font-size: 120%;
  }
  &__links {
    margin-bottom: 2rem;
    text-align: right;
  }
  &__link {
    font-size: 1.8rem;
    color: #229fff;
    text-decoration: underline;
  }
  &__inquiry {
    margin-left: auto;
    padding: 0 1rem;
    border: 2px solid $siteColor;
    border-radius: 1rem;
    width: 29.5rem;
    height: 6.5rem;
    font-size: 1.8rem;
    font-weight: 700;
    color: $fontColor;
    text-align: center;
    letter-spacing: 0.27rem;
    background: #fff;
    gap: 0.8rem;
  }
  &__desc {
    margin-top: 5rem;
    margin-bottom: 6rem;
    width: 63.5rem;
    max-width: 100%;
    &-title {
      position: relative;
      margin-bottom: 2rem;
      padding-bottom: 1rem;
      border-bottom: 1px solid $fontColor;
      font-size: 2.4rem;
      font-weight: 700;
    }
    &-text {
      color: #353535;
    }
  }

  //====================================================
  //
  //  #####    #####  ##   ##  ##  #####  ##      ##
  //  ##  ##   ##     ##   ##  ##  ##     ##      ##
  //  #####    #####  ##   ##  ##  #####  ##  ##  ##
  //  ##  ##   ##      ## ##   ##  ##     ##  ##  ##
  //  ##   ##  #####    ###    ##  #####   ###  ###
  //
  //====================================================
  &__reviews {
    margin-bottom: 6rem;
    &-title {
      margin-bottom: 3rem;
      padding: 1.5rem 2rem;
      border-radius: 0.6rem;
      font-size: 2.4rem;
      font-weight: 500;
      color: #000;
      background: #f2f2f2;
    }
    &-average {
      padding-left: 1rem;
      font-size: 1.6rem;
    }
    &-btn {
      border-radius: 0.5rem;
      width: 29.5rem;
      height: 7.5rem;
      color: #fff;
      background: var(--font-black, #333);
      transition: opacity 0.3s;
    }
    &-icon {
      padding-right: 1rem;
      font-size: 120%;
    }
    // ↓SP相当幅
    @media #{$media-sp} {
      margin-top: 4rem;
      margin-bottom: 4rem;
      &-title {
        padding: 1rem;
        font-size: 1.8rem;
      }
      &-average {
        padding-left: 1rem;
        font-size: 1.6rem;
      }
      &-btn {
        margin: 0 auto;
        width: 27rem;
        height: 6rem;
        font-size: 1.4rem;
      }
      &-icon {
        padding-right: 1rem;
        font-size: 120%;
      }
    }
    // ↑SP相当幅
  }

  // ↓SP相当幅
  @media #{$media-sp} {
    padding-top: 3rem;
    &__title {
      margin-bottom: 4rem;
      padding-bottom: 1.2rem;
      font-size: 2rem;
    }
    &__gallery {
      width: 100%;
      max-width: 100%;
    }
    &__image {
      height: auto;
    }
    &__thumbnail {
      padding-top: 2rem;
      &-img {
        height: 8rem;
      }
    }
    &__main {
      gap: 4rem;
    }
    &__info {
      width: 100%;
      max-width: 100%;
    }
    &__tags {
      margin-bottom: 2rem;
    }
    &__price {
      padding-bottom: 1.5rem;
      width: 100%;
      &-new {
        padding-top: 1rem;
        padding-bottom: 1rem;
        font-size: 3.8rem;
      }
    }
    &__tax {
      font-size: 1.4rem;
    }
    &__order {
      margin-bottom: 5rem;
      &-type {
        margin-bottom: 8px;
      }
    }
    &__radio {
      font-size: 1.8rem;
      input {
        margin-right: 1rem;
        width: 1.6rem;
        height: 1.6rem;
      }
    }
    &__qty {
      &-label {
        padding-right: 1rem;
        font-size: 1.8rem;
      }
      &-btn {
        margin: 0 4px;
        width: 3.5rem;
        font-size: 2rem;
      }
      &-input {
        margin: 0 4px;
        width: 7rem;
      }
    }
    &__subscription-options {
      max-width: 100%;
    }
    &__shipping-msg {
      font-size: 1.4rem;
    }
    &__actions {
      margin-bottom: 2rem;
    }
    &__btn {
      font-size: 2rem;
    }
    &__links {
      margin-bottom: 2rem;
    }
    &__link {
      font-size: 1.6rem;
    }
    &__inquiry {
      width: 27rem;
      height: 5.5rem;
      font-size: 1.6rem;
      letter-spacing: normal;
    }
    &__desc {
      margin-top: 3rem;
      margin-bottom: 2rem;
      padding: 1.5rem;
      border: 1px solid #dadada;
      width: 100%;
      max-width: 100%;
      &-title {
        margin-bottom: 0;
        padding-bottom: 0;
        border-bottom: none;
        font-size: 1.5rem;
        .icon {
          position: absolute;
          right: 0;
        }
        .icon-minus {
          display: none;
        }
      }
      &[open] {
        .icon-plus {
          display: none;
        }
        .icon-minus {
          display: block;
          margin-bottom: 1rem;
        }
        .product-detail__desc-title {
          padding-bottom: 1rem;
        }
      }
      &-text {
        font-size: 1.4rem;
      }
    }
  }
  // ↑SP相当幅
}
