@use "../_core/_core.scss" as *; // コアファイル群

.breadcrumb {
  padding: 0;
  line-height: 1.5;
  background: transparent;
  &__list {
    display: flex;
    flex-wrap: wrap;
    margin: 0;
    padding: 0;
    list-style: none;
  }
  &__item {
    display: flex;
    align-items: center;
    font-weight: 500;
    color: $siteColor;
    opacity: 0.6;
    &--active {
      opacity: 1;
    }
  }
  &__link {
    color: $siteColor;
    text-decoration: none;
    transition: color 0.2s;
  }
  &__separator {
    margin: 0 8px;
    font-size: 80%;
    color: $siteColor;
  }
  // ↓SP相当幅
  @media #{$media-sp} {
    &__item {
      font-size: 1.4rem;
    }
  }
  // ↑SP相当幅
}
