オーバーレイ .mdOverlay

オーバーレイを被せる。「.active」の付与で出現する。
z-index: 1000;とbackground: rgba(#000, .5);は構成に応じて調整。
```
<div id="overlay" class="mdOverlay"></div>

<div id="test-overlay" class="mdOverlay" onclick="this.classList.toggle('active')"></div>
<a onclick="document.getElementById('test-overlay').classList.toggle('active')">テスト開閉</a>

<div id="test-overlay-ripple" class="mdOverlay mdOverlay--ripple" onclick="this.classList.toggle('active')"></div>
<a onclick="document.getElementById('test-overlay-ripple').classList.toggle('active')">テスト開閉（波紋）</a>

<div id="test-overlay-shutter" class="mdOverlay mdOverlay--shutter" onclick="this.classList.toggle('active')"></div>
<a onclick="document.getElementById('test-overlay-shutter').classList.toggle('active')">テスト開閉（シャッター）</a>
```
*/
.mdModal {
  position: fixed;
  top: 50%;
  left: 50%;
  z-index: 50000;
  width: fit-content;
  max-width: calc(100% - 4rem);
  transition-duration: 0.3s;
  pointer-events: none;
}
.mdModal:not([open]) {
  opacity: 0;
  visibility: hidden;
}
.mdModal__box, .mdModal__bg,
.mdModal a,
.mdModal button,
.mdModal iframe {
  pointer-events: auto;
}
.mdModal__bg {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.8);
}
.mdModal__bg:not(.js-modal-close) {
  cursor: default;
}
.mdModal__bg--hide {
  opacity: 0;
}
.mdModal__frame {
  position: relative;
  z-index: 2;
  transform: translate(-50%, -50%);
}
.mdModal__close {
  position: absolute;
  top: -5rem;
  right: 0;
  z-index: 2;
  width: 5rem;
  height: 5rem;
  font-size: 3rem;
  color: #fff;
}
.mdModal__box {
  display: inline-block;
  position: relative;
  z-index: 2;
  max-height: calc(90vh - 5rem);
  overflow-y: auto;
  background: #fff;
}

.mdLoading {
  position: relative;
  min-height: 9rem;
  max-height: 60vh;
}
.mdLoading::before {
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
  z-index: 2;
  margin: -2rem 0 0 -2rem;
  border: 3px solid #0067b6;
  border-bottom-color: rgba(0, 103, 182, 0.5);
  border-radius: 50%;
  width: 4rem;
  height: 4rem;
  animation-name: mdLoading;
  animation-duration: 0.5s;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
  content: "";
}
.mdLoading:not(.active) {
  opacity: 0;
  visibility: hidden;
}

@keyframes mdLoading {
  100% {
    transform: rotate(360deg);
  }
}
/*
#styleguide
ローディングアニメ .mdLoading

円形のローディングアニメを出す。「.active」を消すと非表示になる。
```
<div class="mdLoading active"></div>

```
*/
/*
#styleguide
プレーンリスト　ol.oList ul.uList

ol,ulをデフォルトに近い装飾に戻す。

```
<ul class="uList">
  <li>uList</li>
  <li>テキストテキストテキストテキスト</li>
  <li>テキストテキストテキスト<br>テキストテキストテキスト</li>
</ul>
<ol class="oList">
  <li>oList</li>
  <li>テキストテキストテキストテキスト</li>
  <li>テキストテキストテキスト<br>テキストテキストテキスト</li>
</ol>
```
*/
.uList > li,
.oList > li,
.u-list > li,
.o-list > li {
  margin: 0 0 0 2%;
  width: 98%;
}

.uList > li,
.u-list > li {
  list-style-type: disc;
}

.oList > li,
.o-list > li {
  list-style-type: decimal;
}

.mdLay {
  display: flex;
  position: relative;
  z-index: 1;
  vertical-align: middle;
  transition-duration: 0.3s;
  transform: perspective(0) translate3d(0, 0, 0);
}
.mdLay__bg {
  position: relative;
  z-index: 2;
  width: 100%;
  transition-duration: 0.3s;
}
.mdLay__bg img {
  display: block;
  width: 100%;
}
.mdLay.zoom {
  overflow: hidden;
}
.mdLay__box {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 5;
  width: 100%;
  height: 100%;
}

button.active .mdLay.zoom .mdLay__bg,
a.active .mdLay.zoom .mdLay__bg {
  scale: 1.1;
}

@media screen and (min-width:769px) {
  button.hover .mdLay.zoom .mdLay__bg,
  a.hover .mdLay.zoom .mdLay__bg,
  a:hover .mdLay.zoom .mdLay__bg {
    scale: 1.1;
  }
}
