.mdBtn {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  position: relative;
  z-index: 1;
  width: 100%;
  overflow: hidden;
  line-height: 1;
  text-decoration: none;
  transition-duration: 0.3s;
  cursor: pointer;
  /*
  主に色のバリエーションをつける
  幅、文字サイズ、マージンなど、応用が効かないパラメータはなるべく設定しない
  同じ色だが扱いが特殊なボタンがある時はコピーしてに派生させる　.red → .redContact
  基本的に必ずセットで使用するため、記述コストを下げるため「.mdBtn--black」ではなく「.black」などと書く
  */
}
.mdBtn--mat a,
.mdBtn--mat input[type=submit],
.mdBtn--mat button {
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 2;
  margin: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
}
.mdBtn::before {
  display: inline-block;
  position: absolute;
  top: 0;
  z-index: -1;
  width: 100%;
  height: 100%;
  background: none;
  transition-duration: 0.3s;
  pointer-events: none;
  content: "";
}
.mdBtn::before {
  left: -100%;
}
.mdBtn--r::before {
  left: 100%;
}
.mdBtn--stay:before {
  display: none;
}
.mdBtn:hover, .mdBtn.hover, a:hover .mdBtn, a.hover .mdBtn {
  text-decoration: none;
}
.mdBtn:hover::before, .mdBtn.hover::before, a:hover .mdBtn::before, a.hover .mdBtn::before {
  left: 0;
}
.mdBtn.black {
  border: solid 1px #000;
  color: #fff;
  background-color: #000;
}
.mdBtn.black:before, .mdBtn.black:hover, .mdBtn.black.hover, a:hover .mdBtn.black, a.hover .mdBtn.black {
  color: #000;
  background-color: #fff;
}
.mdBtn.disable {
