@use "../_core/_core.scss" as *; // コアファイル群

.favoriteList {
  margin: 16px;
  margin-right: auto;
  margin-left: auto;
  padding: 24px 16px 32px;
  border-radius: 10px;
  max-width: 900px;
  font-family: "Segoe UI", "Meiryo", sans-serif;
  background: #fff;
  box-shadow: 0 0 0 1px #ddd;

  &__header {
    margin-bottom: 18px;
    padding-bottom: 8px;
    border-bottom: 3px solid #444;
  }

  &__title {
    margin: 0;
    font-size: 1.7rem;
    font-weight: bold;
  }

  &__info {
    margin-bottom: 12px;
    font-size: 14px;
    color: #333;
  }

  &__actions {
    display: flex;
    align-items: center;
    margin-bottom: 16px;

    &--footer {
      justify-content: flex-end;
      margin: 24px 0 0;
    }
  }

  &__checkbox-label {
    display: flex;
    align-items: center;
    margin-right: 14px;
  }

  &__checkbox {
    margin: 0;
    width: 22px;
    height: 22px;
    cursor: pointer;
    accent-color: #333;
  }

  &__delete-btn {
    display: flex;
    align-items: center;
    padding: 9px 18px;
    border: none;
    border-radius: 6px;
    font-size: 15px;
    font-weight: 500;
    color: #fff;
    background: #222;
    transition: background 0.15s;
    cursor: pointer;
    gap: 7px;
    &:hover,
    &:focus {
      background: #444;
    }
  }

  &__delete-icon {
    margin-bottom: 2px;
    font-size: 18px;
    vertical-align: middle;
  }

  &__items {
    margin: 0;
    padding: 0;
    list-style: none;
  }

  &__item {
    display: flex;
    align-items: center;
    position: relative;
    padding: 18px 0;
    border-bottom: 1px solid #eee;
    gap: 18px;

    &:last-child {
      border-bottom: none;
    }
  }

  &__img {
    margin-right: 10px;
    border: 1px solid #ccc;
    border-radius: 7px;
    width: 64px;
    height: 90px;
    background: #fafafa;
    object-fit: cover;
  }

  &__desc {
    display: flex;
    flex: 1 1 auto;
    flex-direction: column;
    min-width: 0;
    gap: 5px;
  }

  &__name {
    margin-bottom: 2px;
    overflow: hidden;
    font-size: 15px;
    font-weight: 600;
    color: #232323;
    text-overflow: ellipsis;
    letter-spacing: 0.01em;
    white-space: nowrap;
  }

  &__price {
    font-size: 14px;
    color: #555;
  }

  &__cart-btn {
    display: flex;
    align-items: center;
    padding: 0 26px;
    border: none;
    border-radius: 7px;
    height: 38px;
    font-size: 16px;
    font-weight: 500;
    color: #fff;
    white-space: nowrap;
    background: #1976d2;
    transition: background 0.15s;
    cursor: pointer;
    gap: 7px;

    &:hover,
    &:focus {
      background: #1565c0;
    }
  }

  &__cart-icon {
    font-size: 18px;
    vertical-align: middle;
  }

  &__pagination {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 4px;
    margin-top: 24px;
  }

  &__page-btn {
    margin: 0 1px;
    padding: 0 9px;
    border: 1.7px solid #1976d2;
    border-radius: 5px;
    min-width: 28px;
    height: 28px;
    font-size: 15px;
    font-weight: 500;
    color: #1976d2;
    background: #fff;
    outline: none;
    transition: background 0.13s, color 0.13s, border-color 0.13s;
    cursor: pointer;

    &:hover,
    &:focus {
      border-color: #1565c0;
      color: #1565c0;
      background: #e3f2fd;
    }

    &:disabled {
      border-color: #eee;
      color: #bbb;
      background: #fafbfc;
      cursor: default;
    }

    &--active {
      border-color: #1976d2;
      color: #fff;
      background: #1976d2;
      pointer-events: none;
      cursor: default;
    }
  }

  &__page-ellipsis {
    padding: 0 5px;
    font-size: 18px;
    color: #888;
  }
}
