@use "../_core/_core.scss" as *; // コアファイル群
.uLineAnime {
  position: relative;
  text-decoration: none;
  &::before {
    display: block;
    position: absolute;
    bottom: 0;
    left: 0;
    z-index: 5;
    width: 100%;
    height: 2px;
    background: $siteColor;
    transition-duration: 0.5s;
    transform: rotateY(90deg);
    transform-origin: 50%;
    content: "";
  }
  &.uline-block {
    display: block;
  }
  &.uline-left::before {
    transform-origin: 0%;
  }
  &.uline-right::before {
    transform-origin: 100%;
  }
}
a.active .uLineAnime,
.uLineAnime.active,
a:hover .uLineAnime,
.uLineAnime:hover {
  text-decoration: none;
  &:before {
    transform: rotateY(0deg);
  }
}

/*
#styleguide
アンダーライン .uLineAnime

アニメーションでアンダーラインを乗せる。Aタグのホバーか「.active」に反応する。
```
