@use "_core/_core.scss" as *; // コアファイル群
// ヘッダーの高さ
$headerPC: 20rem;
$headerSP: 6rem;

/*
########     ###    ########
##     ##   ## ##   ##     ##
########  ##     ## ##     ##
##        ######### ##     ##
##        ##     ## ########
*/
// 追従ヘッダー用の余白
.headerPad {
  padding-top: $headerPC;
  // ↓SP相当幅
  @media #{$media-sp} {
    padding-top: $headerSP;
  }
  // ↑SP相当幅
}

/*
##     ## ########    ###    ########  ######## ########
##     ## ##        ##   ##  ##     ## ##       ##     ##
######### ######   ##     ## ##     ## ######   ########
##     ## ##       ######### ##     ## ##       ##   ##
##     ## ######## ##     ## ########  ######## ##     ##
*/
.header {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1000;
  width: 100%;
  white-space: nowrap;
  background-color: #fff;
  box-shadow: 0 10px 18px 0 rgba(0, 0, 0, 0.05);
  &__topbase {
    // ↓SP以外の幅
    @media #{$media-notsp} {
      padding-top: 0.5rem;
    }
    // ↑SP以外の幅
  }
  &__frame {
  }
  &__base {
  }
  &__topbase {
    // ↓SP以外の幅
    @media #{$media-notsp} {
      padding-bottom: 1rem;
    }
    // ↑SP以外の幅
  }
  &__topleft {
  }
  &__topright {
    gap: 3rem;
    // ↓SP以外の幅
    @media #{$media-notsp} {
      padding-top: 2rem;
    }
    // ↑SP以外の幅
  }
  &__bottombase {
    padding-top: 1rem;
    padding-bottom: 1rem;
    border-top: 1px solid #dadada;
  }
  &__desc {
    padding-bottom: 1rem;
    font-size: min(1.2rem, 1vw);
    font-weight: 400;
    color: $colorGray;
  }
  &Logo {
    &__img {
    }
    &__text {
      padding-top: 1rem;
      padding-left: 1rem;
      font-size: 1.3rem;
      color: #be0000;
    }
  }
  &Search {
    &__input {
      padding: 1.2rem 1rem;
      border: 0.662px solid var(--line-gray, #dadada);
      border-radius: 0.5rem 0 0 0.5rem;
      width: min(28.4rem, 22vw);
      height: 4.5rem;
      background: var(--background-gray, #f2f2f2);
      &::placeholder {
        font-weight: 400;
        color: $colorGray;
      }
    }
    &__btn {
      padding: 0.9rem 1.3rem;
      border-radius: 0 0.5rem 0.5rem 0;
      width: 9.3rem;
      height: 4.5rem;
      font-weight: 400;
      color: #fff;
      background-color: $siteColor;
      gap: 0.5rem;
      transition-duration: 0.3s;
      &:hover {
        opacity: 0.8;
      }
      .icon {
        padding-right: 0.5rem;
        font-size: 140%;
      }
    }
  }
  &Tel {
    &__txt {
      font-size: 1.4rem;
      color: $colorGray;
    }
    &__line {
      gap: 1rem;
    }
    &__num {
      font-size: 2.8rem;
      font-weight: 600;
      line-height: 1.4;
      color: $siteColor;
    }
    &__ttl {
      padding: 0 0.4rem 0.2rem;
      border-radius: 0.3rem;
      line-height: 1.4;
      color: #fff;
      background-color: $siteColor;
    }
  }
  &Nav {
    &__link {
      font-size: min(1.6rem, 1.2vw);
      color: $fontColor;
      &--icon {
        padding-right: 0.5rem;
        font-size: 120%;
        color: $siteColor;
      }
      &--arrow {
        padding-top: 0.6rem;
        font-size: 70%;
      }
    }
    &__btn {
      position: relative;
      padding-right: 1.5rem;
      padding-left: 1.5rem;
      border-bottom: 3px solid transparent;
      border-left: 1px solid #dadada;
      height: 6.5rem;
      transition-duration: 0.3s;
      &:last-child {
        border-right: 1px solid #dadada;
      }
      &-grp {
        max-width: 27.7rem;
        line-height: 1.3;
        white-space: normal;
      }
      &:hover {
        border-bottom-color: $siteColor;
      }
    }
  }

  &Btn {
    gap: 1rem;
    &__link {
      padding: 1.6rem 1.8rem;
      border: 1px solid var(--line-gray, #dadada);
      border-radius: 0.5rem;
      width: 12rem;
      height: 7.5rem;
      font-weight: 400;
      color: $fontColor;
      color: #fff;
      text-align: center;
      .icon {
        font-size: 120%;
      }
      &--reg,
      &--mypage {
        color: $fontColor;
        background-color: #f2f2f2;
      }
      &--login {
        background-color: $fontColor;
      }
      &--fav {
        background-color: $colorPink;
      }
      &--cart {
        font-size: 1.4rem;
        background-color: $siteColor;
      }
    }
  }
  /*
               ######  ########
              ##       ##     ##
      #######  ######  ########
                    ## ##
               ######  ##
  */
  // ↓sp相当幅
  @media #{$media-sp} {
    max-height: 100%;
    overflow: auto;
    &__frame {
      padding: 0 0 0 $pad;
    }
    &__base {
    }
    &Search {
      &__input {
        width: calc(100% - 9.3rem);
        max-width: 100%;
        background-color: #fff;
        &::placeholder {
          font-size: 1.5rem;
        }
      }
    }
    &Btn {
      &__link {
        margin-bottom: 4rem;
        width: 100%;
        height: 6rem;
        gap: 0.6rem;
        &--login {
          font-size: 2rem;
        }
      }
    }
  }
  // ↑SP相当幅
}

/*
##        #######   ######    #######
##       ##     ## ##        ##     ##
##       ##     ## ##   #### ##     ##
##       ##     ## ##    ##  ##     ##
########  #######   ######    #######
*/
.headerLogo {
  &__img {
    display: block;
  }
  &__text {
  }

  // ↓ SP相当幅
  @media #{$media-sp} {
    &__img {
      max-width: 18.8rem;
    }
    &__text {
    }
  }
  // ↑ SP相当幅
}

/*
 ########  ##     ## ##       ##       ########   #######  ##      ## ##    ##
 ##     ## ##     ## ##       ##       ##     ## ##     ## ##  ##  ## ####  ##
 ########  ##     ## ##       ##       ##     ## ##     ## ##  ##  ## ## ## ##
 ##        ##     ## ##       ##       ##     ## ##     ## ##  ##  ## ##  ####
 ##         #######  ######## ######## ########   #######   ###  ###  ##    ##
*/
// プルダウンメニュー
.headerPulldown {
  &__head {
    padding: 1rem 2.5rem;
    height: $headerPC;
    font-weight: 500;
    line-height: 1;
    color: #fff;
    transition-duration: 0.2s;
    &:not(a) {
      cursor: default;
    }
  }
  &__body {
    position: absolute;
    top: 100%;
    left: 0;
    z-index: 2;
    margin-top: 3px;
    border: 1px solid var(--line-gray, #dadada);
    width: 26.4rem;
    background-color: #fff;
    box-shadow: 0 10px 18px 0 rgba(0, 0, 0, 0.05);
    opacity: 0;
    transition-duration: 0.3s;
    transform: rotateX(90deg);
    transform-origin: 0 0;
    .parent {
      position: relative;
      &:hover {
        background-color: #f2f2f2;
        & > ul {
          display: grid;
          grid-template-columns: repeat(4, 1fr);
          position: absolute;
          border-top: 1px solid #dadada;
          &.-col1 {
            grid-template-columns: repeat(1, 1fr);
          }
          &.-col2 {
            grid-template-columns: repeat(2, 1fr);
          }
        }
      }
      li {
        font-size: 1.3rem;
      }
    }
    .child {
      display: none;
      top: 0;
      left: 100%;
      margin-left: 1px;
    }
    ul {
      & > {
        li {
          display: flex;
          justify-content: space-between;
          align-items: center;
          padding: 1.5rem;
          border-right: 1px solid #dadada;
          border-bottom: 1px solid #dadada;
          width: 26.4rem;
          height: 4.9rem;
          font-size: 1.5rem;
          background-color: #fff;
          &:not(:last-child) {
          }
          a {
            display: -webkit-box;
            overflow: hidden;
            font-weight: 500;
            color: #333;
            text-overflow: ellipsis;
            -webkit-line-clamp: 1;
            -webkit-box-orient: vertical;
          }
          &:is(.parent) {
            &::after {
              font-family: iconfont !important;
              content: "\f102";
            }
            li {
              width: 21.2rem;
            }
          }
          &:hover {
            background-color: #f2f2f2;
          }
        }
      }
    }
  }
  &__link {
    padding: 0 1.5rem;
    height: 4.9rem;
    font-size: 1.5rem;
    color: $fontColor;
    &:not(:last-child) {
      border-bottom: 1px solid #dadada;
    }
    &--arrow {
      padding-top: 0.4rem;
      font-size: 80%;
    }
  }

  &:hover {
    .headerPulldown {
      &__head {
        &::before {
          transform: none;
        }
      }
      &__body {
        opacity: 1;
        transform: rotateX(0);
      }
    }
  }
}

/*
 ######           ##    ##    ###    ##     ##
##                ####  ##  ##   ##  ##     ##
##   #### ####### ## ## ## ##     ## ##     ##
##    ##          ##  #### #########  ##   ##
 ######           ##    ## ##     ##    ###
*/

/*
        ########  ######## ##    ##
        ##     ##    ##    ####  ##
####### ########     ##    ## ## ##
        ##     ##    ##    ##  ####
        ########     ##    ##    ##
*/
// グローバルナビ
.gNavBtn {
  $btnSize: $headerSP; //ボタン一辺の長さ
  $bar: 3px; //バーの太さ
  $posX: 1.5rem; //バーの左右の余白
  $posY: 1.75rem; //1,3本目のバーの上下の余白
  $barRadius: 0; //バーの角丸
  $barOffBG: #fff; //バーの背景OFF時
  $barOnBG: #fff; //バーの背景ON時
  $time: 0.3s; //アニメ時間　0.1s以上
  display: inline-block;
  position: relative;
  border: 0;
  width: $btnSize;
  height: $btnSize;
  font-size: 1rem;
  font-weight: 700;
  color: $siteColor;
  letter-spacing: 0.05rem;
  vertical-align: middle;
  cursor: pointer;
  &__box {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 1;
    width: 100%;
    height: 100%;
  }
  &__bar {
    display: block;
    position: absolute;
    left: $posX;
    z-index: 2;
    border-radius: $barRadius;
    width: calc(100% - #{$posX} * 2);
    height: $bar;
    background: $barOffBG;
    transition-duration: $time;
    &:nth-of-type(1) {
      top: $posY;
    }
    &:nth-of-type(2) {
      top: 50%;
      margin-top: div($bar, -2);
    }
    &:nth-of-type(3) {
      bottom: $posY;
    }
  }
  &__icon {
    font-size: 3rem;
    transform-style: preserve-3d;
    transition: transform 0.3s ease-in-out;
    will-change: transform;
  }
  .-close {
    display: none;
  }
  &.active {
    .gNavBtn {
      &__icon {
        transform: rotateY(180deg);
      }
      &__bar {
        background: $barOnBG;
        &:nth-of-type(1) {
          top: 50%;
          margin-top: div($bar, -2);
          transition: top $time, margin $time, rotate $time ease $time;
          rotate: -45deg;
        }
        &:nth-of-type(2) {
          opacity: 0;
          transform: rotateY(90deg);
        }
        &:nth-of-type(3) {
          bottom: 50%;
          margin-bottom: div($bar, -2);
          transition: bottom $time, margin $time, rotate $time ease $time;
          rotate: 45deg;
        }
      }
    }
    .-menu {
      display: none;
    }
    .-close {
      display: block;
    }
  }
}
/*
        ##    ##    ###    ##     ##
        ####  ##  ##   ##  ##     ##
####### ## ## ## ##     ## ##     ##
        ##  #### #########  ##   ##
        ##    ## ##     ##    ###
*/
.gNav {
  position: fixed;
  top: $headerSP;
  left: 0;
  z-index: 1000;
  width: 100%;
  max-height: calc(100% - #{$headerSP});
  overflow-y: auto;

  background: #f2f2f2;
  transition-duration: 0.3s;
  &::after {
    // 下の余白用疑似要素
    display: block;
    height: 0;
    content: "";
  }
  // ↓SP以外の幅
  @media #{$media-notsp} {
    top: $headerPC;
  }
  // ↑SP以外の幅
  &:not(.active) {
    translate: 100% 0%;
    visibility: hidden;
  }
  &__menu {
  }
  &__box {
    margin-top: 2rem;
  }
  &__item {
    position: relative;
    padding: 1.5rem;
    border-bottom: 1px solid #dadada;
    font-weight: 500;
    line-height: 1.4;
    color: $fontColor;
    gap: 1rem;
    .icon {
      font-size: 140%;
    }
    &:is(.-sub) {
      border-radius: 0.5rem 0.5rem 0 0;
      color: #fff;
      background: $siteColor;
      .icon {
        color: #fff;
      }
    }
    &--sub {
      font-size: 1.5rem;
      color: $fontColor;
      background: #fff;
      &.-level2 {
        background: #eff7ff;
      }
    }
    &--notsub {
      margin-top: 2rem;
      border: 2px solid $siteColor;
      border-radius: 1rem;
    }
    .icon-small {
      font-size: 70%;
    }
  }
  &__icon {
    position: absolute;
    right: 2rem;
    z-index: 2;
    font-size: 1rem;
    transition-duration: 0.3s;
    &--minus {
      margin-bottom: 6px;
      opacity: 0;
      visibility: hidden;
      .mdAcc__check:checked ~ .mdAcc__btn & {
        opacity: 1;
        visibility: visible;
      }
    }
    &--plus {
      .mdAcc__check:checked ~ .mdAcc__btn & {
        opacity: 0;
        visibility: hidden;
      }
    }
    .mdAcc__check:checked ~ .mdAcc__btn & {
      // rotate: 180deg;
    }
  }
  &__contact {
    padding: 1.5rem $pad 3rem;
    &--txt {
      font-size: 1.4rem;
      font-weight: 500;
      color: #626161;
    }
    &--tel {
      line-height: 1.4;
      span {
        &:nth-child(1) {
          margin-right: 1rem;
          padding: 0 0.5rem;
          border-radius: 0.3rem;
          font-size: 1.2rem;
          font-weight: 500;
          color: #fff;
          background-color: $siteColor;
        }
        &:nth-child(2) {
          font-size: 2rem;
          font-weight: 600;
          color: $siteColor;
        }
      }
    }
  }
  &__btn {
    &--mail {
      height: 6rem;
    }
  }
}
