@use "../_core/_core.scss" as *; // コアファイル群
$mdBtnAnime: $animeTime;
.mdBtn {
  display: inline-flex;
  justify-content: center; //水平上の中央に寄せる
  align-items: center; //中段に揃える
  position: relative;
  z-index: 1;
  width: 100%;
  overflow: hidden;
  line-height: 1;
  text-decoration: none;
  transition-duration: $mdBtnAnime;
  cursor: pointer;

  // aタグやbutton類のタグを直接編集できない場合に使用する
  &--mat {
    a,
    input[type="submit"],
    button {
      display: block;
      position: absolute;
      top: 0;
      left: 0;
      z-index: 2;
      margin: 0;
      width: 100%;
      height: 100%;
      opacity: 0;
    }
  }
  // スライドアニメ演出用要素
  &::before {
    display: inline-block;
    position: absolute;
    top: 0;
    z-index: -1;
    width: 100%;
    height: 100%;
    background: none;
    transition-duration: $mdBtnAnime;
    pointer-events: none;
    content: "";
  }

  &::before {
    left: -100%; // デフォルト　左から右へスライド
  }
  &--r::before {
    // スライドアニメーションを反転する
    left: 100%; // 右から左へスライド
  }

  // スライドアニメを行わない
  &--stay:before {
    display: none;
  }

  &:hover,
  &.hover,
  a:hover &,
  a.hover & {
    text-decoration: none;
    &::before {
      left: 0;
    }
  }

  //   ####   #####   ##       #####   #####
  //  ##     ##   ##  ##      ##   ##  ##  ##
  //  ##     ##   ##  ##      ##   ##  #####
  //  ##     ##   ##  ##      ##   ##  ##  ##
  //   ####   #####   ######   #####   ##   ##
  /*
  主に色のバリエーションをつける
  幅、文字サイズ、マージンなど、応用が効かないパラメータはなるべく設定しない
  同じ色だが扱いが特殊なボタンがある時はコピーしてに派生させる　.red → .redContact
  基本的に必ずセットで使用するため、記述コストを下げるため「.mdBtn--black」ではなく「.black」などと書く
  */
  &.black {
    border: solid 1px #000;
    color: #fff;
    background-color: #000;

    &:before,
    &:hover,
    &.hover,
    a:hover &,
    a.hover & {
      color: #000;
      background-color: #fff;
    }
  }

  //   #####   #####   ######  ##   #####   ##     ##
  //  ##   ##  ##  ##    ##    ##  ##   ##  ####   ##
  //  ##   ##  #####     ##    ##  ##   ##  ##  ## ##
  //  ##   ##  ##        ##    ##  ##   ##  ##    ###
  //   #####   ##        ##    ##   #####   ##     ##
  // その他の状態変化
  &.disable {
    // 無効化
    filter: grayscale(100%);
    opacity: 0.5;
    pointer-events: none;
  }
}

/*
#styleguide
ボタン .mdBtn

指定したタグをボタン風にする。
マウスホバーもしくは「active」クラスでアニメーションする。aタグの小要素に使っている場合は、親aタグの状態にも反応する。
「mat」を付けることで、直下のボタン要素を装飾することもできる。
動く系のアニメは「stay」で止められる。サイト全体でアニメーションが不要な場合はbefore要素を削除する。
```
<a class="mdBtn black" href="#">
  次のページ
</a>

<br><br><br>

<span class="mdBtn mdBtn--mat mdBtn--stay black">
入力内容を確認する
  <input type="submit" value="次へ">
</span>

<br><br><br>

<a class="mdBtn black reverse" href="#">
  戻る
</a>

<br><br><br>

<a class="mdBtn black left-text" href="#">
  テキストテキスト<br>テキスト
</a>
```
*/
