@use "../_core/_core.scss" as *; // コアファイル群

/*
#styleguide
プレーンリスト　ol.oList ul.uList

ol,ulをデフォルトに近い装飾に戻す。

```
<ul class="uList">
  <li>uList</li>
  <li>テキストテキストテキストテキスト</li>
  <li>テキストテキストテキスト<br>テキストテキストテキスト</li>
</ul>
<ol class="oList">
  <li>oList</li>
  <li>テキストテキストテキストテキスト</li>
  <li>テキストテキストテキスト<br>テキストテキストテキスト</li>
</ol>
```
*/
.uList,
.oList,
.u-list,
.o-list {
  > li {
    margin: 0 0 0 2%;
    width: 98%;
  }
}

.uList,
.u-list {
  > li {
    list-style-type: disc;
  }
}

.oList,
.o-list {
  > li {
    list-style-type: decimal;
  }
}
