@use "../_core/_core.scss" as *; // コアファイル群

.orderList {
  width: 100%;
  &__table {
    width: 100%;
    background: #fff;
    border-collapse: collapse;
  }
  &__head {
    background: #f3f3f3;
  }
  &__row {
    border-bottom: 1px solid #e0e0e0;
    &--head {
      border-bottom: 2px solid #e0e0e0;
    }
  }
  &__cell {
    padding: 16px;
    font-size: 16px;
    color: #333;
    text-align: left;
    vertical-align: middle;
    &--head {
      font-size: 15px;
      font-weight: 600;
      color: #999;
      background: #f3f3f3;
    }
  }
  &__detail {
    &-btn {
      display: flex;
      justify-content: center;
      align-items: center;
      padding: 0 18px;
      border: 2px solid #1565c0;
      border-radius: 8px;
      min-width: 120px;
      height: 40px;
      font-size: 16px;
      font-weight: bold;
      color: #232323;
      background: #fff;
      outline: none;
      transition: background 0.1s, border-color 0.1s;
      cursor: pointer;
    }
    &-icon {
      display: inline-block;
      margin-left: 6px;
      font-size: 16px;
      font-weight: bold;
    }
  }
}
