@use "../_core/_core.scss" as *; // コアファイル群

.mypageSidebar {
  padding: 0;
  width: 100%;
  max-width: 350px;
  font-family: "Segoe UI", "Meiryo", sans-serif;
  background: #fff;

  &__title {
    margin: 24px 0 16px 16px;
    font-size: 1.5rem;
    font-weight: bold;
    color: #232323;
  }

  &__list {
    margin: 0;
    padding: 0;
    list-style: none;
  }

  &__item {
    border-bottom: 1px solid #ececec;
    background: #fff;

    &--active {
      .mypage-menu__link {
        border-left: 4px solid #1976d2;
        font-weight: 600;
        color: #1976d2;
        background: #f5faff;
      }
    }
  }

  &__link {
    display: block;
    padding: 20px 0 20px 24px;
    border-left: 4px solid transparent;
    font-size: 18px;
    color: #222;
    text-decoration: none;
    transition: background 0.13s, color 0.13s, border-color 0.13s;

    &:hover,
    &:focus {
      color: #1976d2;
      background: #f5faff;
    }
  }

  &__logout-btn {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 28px auto 0;
    padding: 18px 0;
    border: none;
    border-radius: 7px;
    width: 90%;
    font-size: 18px;
    font-weight: 500;
    color: #fff;
    background: #2d2d2d;
    transition: background 0.13s;
    cursor: pointer;
    gap: 10px;

    &:hover,
    &:focus {
      background: #444;
    }
  }

  &__logout-icon {
    margin-right: 2px;
    font-family: inherit;
    font-size: 22px;
    vertical-align: middle;
  }
}
